html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
* {
  font-family: 'Poppins', sans-serif;
}

body {
  font-size: 14px;
  background-color: #d9d9d9;
}

body > img {
  margin-top: 100px;
}

p {
  text-align: justify;
}

h1, h2, h3,
h4,
h5,
h6 {
  font-size: 2em;
  color: #6e1541;
  text-align: center;
  font-family: 'Tangerine';
}

h1, h2, h4 {
  margin-top: 5px;
  margin-bottom: -10px;
  font-size: 2.6em;
  color: #BA5C8A;
}

nav.principal {
  color: white;
  min-height: 120px;
  display: flex;
  align-items: center;
  background-color: #6e1541;
}

nav.principal .logo {
  width: 20%;
}

nav.principal .logo img {
  display: block;
  width: 80%;
  margin: auto;
}

nav.principal .contact-info {
  width: 20%;
}

nav.principal .contact-info p {
  margin: 0px;
  position: relative;
  bottom: -30px;
}

nav.principal .contact-info p a {
  color: white;
  text-decoration: none;
}

nav.principal .menu {
  width: 60%;
}

nav.principal .menu ul {
  display: flex;
  float: right;
}

nav.principal .menu ul li {
  width: 150px;
  list-style: none;
}

nav.principal .menu ul li a {
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
  transition: all 500ms;
  font-family: 'Satisfy', cursive;
}

nav.res {
  background-color: #6e1541;
  width: 100%;
  height: 70px;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1000;
}

nav.res button {
  width: 30%;
}

nav.res button.button {
  margin: auto;
  padding: 2px;
  width: 50px;
  height: 50px;
  display: block;
  background-color: #BA5C8A;
  border-radius: 5px;
}

nav.res p {
  width: 70%;
  color: white;
  font-size: 2em;
  font-weight: bold;
  font-family: 'Tangerine', cursive;
}

nav.sub-menu {
  background-color: white;
  border-radius: 10px;
  padding: 0px;
}

nav.sub-menu h3 {
  width: 90%;
  margin: auto;
  padding-top: 10px;
  margin-bottom: 0px;
  font-size: 2.1em;
  color: #BA5C8A;
  border-bottom: #BA5C8A solid 3px;
}

nav.sub-menu ul {
  margin-top: 0px;
  padding: 10px;
}

nav.sub-menu ul li {
  padding: 8px;
  margin: 8px auto;
  overflow: hidden;
  cursor: pointer;
}

nav.sub-menu ul li::before {
  content: "-";
}

nav.sub-menu ul li::after {
  content: "";
  display: block;
  top: 5px;
  left: -400px;
  position: relative;
  width: 100%;
  border-radius: 0px 50% 50% 0px;
  border-bottom: solid 3px #BA5C8A;
  transition: 300ms;
}

nav.sub-menu ul li:hover::after {
  position: relative;
  left: -10px;
}

nav.sub-menu ul li a {
  padding-left: 10px;
  font-size: 1em;
  color: #6e1541;
  text-decoration: none;
}

