  margin: 15px auto;
  list-style: none;
  text-align: center;
}

.off-canvas ul li a {
  color: white;
  text-decoration: none;
}

.js-off-canvas-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 0.5s ease, visibility 0.5s ease;
  background: rgba(0, 0, 0, 0.6);
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
}

.off-canvas-content, .off-canvas-content {
